.custom-select {
  margin-right: 1rem;
  width: min(80%, 350px);

  &--sort {
    @include flex-all-sb;

    margin: 0;
    gap: 10px;
    width: min(80%, 235px);
  }

  &--show {
    @include flex-all-sb;
    margin: 0;
    width: min(80%, 255px);
    gap: 10px;
  }

  &__select {
    display: block;
    grid-area: select;
    width: 100%;

    option {
      white-space: normal;

      // Only affects Chrome
      outline-color: var(--color-green);
    }
  }

  &__inner {
    position: relative;
    display: grid;
    align-items: center;
    grid-template-areas: "select";
    border: 1px solid var(--color-gray-400);
    border-radius: var(--br);
    padding: 12px 12px 12px 16px;

    &::after {
      --rotate: rotate(0);

      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      justify-self: end;
      grid-area: select;
      mask-image: url("../img/sprite.svg#arrow-down");
      mask-position: center;
      mask-repeat: no-repeat;
      mask-size: cover;
      width: 16px;
      height: 16px;
      background-color: var(--color-gray-800);
      transform: translateY(-50%) var(--rotate);
      transition:
        background-color 0.3s linear,
        transform 0.3s linear;
      pointer-events: none;
    }
  }

  &__lable {
    font-weight: 700;
    line-height: 1.6;
    white-space: nowrap;
  }

  &__span {
    font-size: 14px;
    color: var(--color-gray-700);
    white-space: nowrap;
  }
}

.input__number {
  width: 100%;
  max-width: 80px;
  position: relative;
  border: 1px solid var(--color-gray-400);
  border-radius: var(--br);
  padding: em(12) em(15);
  outline-color: transparent;

  @include for-width (-mobile-s) {
    order: -2;
  }
}

input[type="number"]::-webkit-outer-spin-button {
  content: "";
  -webkit-appearance: none;
  background-color: transparent;
  position: absolute;
  width: 1rem;
  height: 1rem;
  background-color: var(--color-gray-800);
  clip-path: polygon(100% 0%, 0 0%, 50% 100%);
}

.table {
  width: 100%;
  border: 1px solid var(--color-gray-300);
  table-layout: fixed;


  th,
  td {
    padding: 12px 2px;
    text-align: left;

    @include for-width(-mobile-md) {
      padding: 2px;
      text-align: center;
    }
  }

  th {
    padding: 12px;
    position: sticky;
    left: 0;
  }


  tr:nth-child(2n + 1) {
    background-color: var(--color-gray-200);
  }

  caption {
    font-weight: 700;
    font-size: 18px;
    text-align: center;
  }

