.burger {
  display: none;

  @include for-width(-mobile-l) {
    --button-color: var(--color-green);

    @include flex-all-center;

    width: 24px;
    height: 24px;

    &[data-state="closed"] :is(.top, .bottom) {
      animation: to-open-icon 1s forwards;
    }

    &[data-state="opened"] :is(.top, .bottom) {
      animation: to-close-icon 1s forwards;
    }

    &[data-state="opened"] :is(.middle) {
      opacity: 0;
      transition: opacity 0.1s linear;
    }

    & .burger__body {
      transition: rotate 0.3s 0.1s linear;
    }

    &[data-state="opened"] .burger__body {
      rotate: 1turn;
    }

    & .line.top {
      --rotation: -45deg;
      transform-origin: 65px 45px;
    }

    & .line.bottom {
      --rotation: 45deg;
      transform-origin: 80px 60px;
    }

    @keyframes to-close-icon {
      0% {
        stroke-dashoffset: 0;
      }

      40% {
        stroke-dashoffset: 79.9;
      }

      60% {
        stroke-dashoffset: 79.9;
        rotate: calc(var(--rotation));
      }

      100% {
        stroke-dashoffset: 0;
        rotate: var(--rotation);
      }
    }

    @keyframes to-open-icon {
      0% {
        stroke-dashoffset: 0;
        rotate: var(--rotation);
      }

      40% {
        stroke-dashoffset: 79.9;
        rotate: var(--rotation);
      }

      60% {
        stroke-dashoffset: 79.9;
      }

      100% {
        stroke-dashoffset: 0;
      }
    }
  }
}


